.bookmark-wrapper{
  position: relative;
}
.prev-icon {
  position:fixed;
  top: 5%;
  left: 10%;
}
.bookmarks-content {
  display:flex;
  flex-direction: column;
  align-items: center;
  background: black;
  padding-top: 25%;
  height : 100vh;////////
  // opacity : 0;/////////
  overflow-y: scroll;
  .image-content{
    width: 80%;
    margin-bottom: 10px;
    figcaption {
      position: absolute;    
      left: 1%;
      width: 50%;
      height:20%;
      background: rgba(0, 0, 0, 0.787);
      border-radius: 2px;
      h2 {
        color: white;
        font-size: 1.1em;
      }
    }
    .buttonAddRemove{
      position: absolute;
      display: flex;
      justify-content: space-between;
      top: 90%;
      left: 15%;
      width: 70%;
      box-shadow: none;    
      button {
        background: #f5471c;
        font-size: 1.3rem;
        color: white;
        border-radius: 3px;
        border: none; 
        &:first-child {
          background: #4a4a4a;
        }
        &:hover{
          opacity: 0.8;
        }
      }
    }
  }
}

.alertMessage {
  height: 76vh;
  border-bottom: 1px solid white;
  width: 98.3vw;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}
// Small Tablet screen
@media screen and (min-width: 768px){
  .bookmarks-content {
    position : relative;
    display: grid;
    grid-template-columns: 1fr 4fr 4fr 1fr;
    padding-top: 15%;
  
    h3{
      position : absolute;
      top : 50%;
      left: 50%;
      transform: translate(-50%,-50%);

    }
    .image-content {

      &:nth-child(even){
        grid-column: 3 / span 1;
        
      }
      &:nth-child(odd){
        grid-column: 2 / span 1;
      }

      img.image {
        max-width: 100%;
      }

      figcaption {
        h2 {
        font-size: 1.4em;
        }
      }   
      .buttonAddRemove{
        height: 23px;
      }
      button {
        cursor: pointer;
      }
    }
  }    
}

@media screen and (min-width: 900px) {
  figcaption.text-content {
    top: 0 !important;
    left: 10% !important;
  }
}
// Large screen
@media screen and (min-width: 1024px) {

  figcaption {
    height: 5%;
  }
  footer {
    margin-top: 0;
  }

  .prev-icon {
    padding-top: 10%;
  }
  
  button {
    width: 60px;
  }
}
  
   
    

